.Field {
    border-color: black;
    height: 20vh;
    width: 60vw;
    display: flex;
    flex-direction: row;
    position: absolute;
    bottom: 60%;
    left: 20vw;
    justify-content: center;
    align-items: center;
    /* overflow-x: scroll; */
}

.Field::-webkit-scrollbar-track {
    background: linear-gradient(90deg,#201c29,#201c29 1px,#17141d 0,#17141d);
}