.Submit {
    border-color: black;
    height: 13vh;
    width: 30vw;
    display: flex;
    flex-direction: row;
    position: absolute;
    bottom: 35%;
    left: 35vw;
    justify-content: center;
    align-items: center;
    background-color: #EFE7DA;
    border-radius: 16px;
    box-shadow: -0.1rem 0 3rem #000;
    color: #E55812;
    font-size: 32pt;
    font-style: oblique;
    font-weight: 700;
}

.Submit:hover{
    outline: none;
    border-radius: 10pt;
    border: 2px solid #E55812;
}

.Submit:disabled{
    outline: none;
    border-radius: 10pt;
    background-color: #d6d0c7;
    color: #5e5e5e;
    border: 2px solid #000000;
}

.Submit:active {
    background-color: #f5ead8;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
  }

