.Card {
    background-color: #EFE7DA;
    height: 100%;
    width: 13pt;
    min-width: 80pt;
    margin: 5pt;
    border-radius: 16px;
    box-shadow: -0.1rem 0 3rem #000;
    transition: .15s;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 32pt;
    font-style: oblique;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #E55812;
    font-weight: 700;


}

.Card:hover {
    outline: none;
    border-radius: 10pt;
    border: 2px solid #E55812;
    transform: translateY(-1rem);

}